<template>
    <div class="login">
        <top-title :type="true"></top-title>
        <f-alert></f-alert>
        <div class="login-bg" :style="{backgroundImage:`url('/static/user/bg2.jpg')`}">
            <div class="login-box">
                <div class="box-title">
                    <p>欢迎使用福分通平台</p>
                    <p style="font-size: 16px;margin-top: 10px">卡券兑换</p>
                </div>
                <div class="box-form">
                    <div class="user  form-item">
                        <label for="card">
                            <input id="card" type="text"
                                   name="卡号"
                                   placeholder="请输入兑换卡卡号"
                                   v-model="card_num">
                        </label>
                    </div>
                    <div class="user  form-item">
                        <label for="card-pwd">
                            <input id="card-pwd" type="text" name="用户名" placeholder="请输入兑换卡密码"
                                   v-model="card_pwd">
                        </label>
                    </div>
                    <div class="user  form-item">
                        <label for="user-id">
                            <input id="user-id" type="text" name="用户名" placeholder="请输入手机号码"
                                   v-model="phone">
                        </label>
                    </div>
                    <div class="message form-item">
                        <label for="msg">
                            <input id="msg" type="text" name="验证码" placeholder="请输入验证码" v-model="password"
                                   @keyup.enter="login()">
                        </label>
                        <div class="decode">
                            <verifycode :phone="phone"></verifycode>
                        </div>
                    </div>
                    <div class="sign-in form-item" @click="checkInput()">
                        <input type="button" value="登录" >
                    </div>
                    <!--<div class="save-login">-->
                        <!--<a class="fl" v-if="type===2" @click="changeType">使用密码登录</a>-->
                        <!--<a class="fl" v-if="type===1" @click="changeType">使用验证码登录</a>-->
                        <!--<a class="fr" href="/user/blogin.html">-->
                            <!--切换到企业用户登录-->
                        <!--</a>-->
                    <!--</div>-->
                </div>
            </div>
            <div class="login-info">
                <p style="margin-bottom: 10px">关于我们 | 福分通商城 | 增值通 | 维修通 | officemate办公伙伴</p>
                <p>版权所有 © CopyRight 2017 欧菲斯办公伙伴控股有限公司 渝ICP备18008342号-2</p>
            </div>
        </div>
    </div>
</template>

<script>
    import TopTitle from '../../../components/TopTile'
    import FAlert from '../../../components/FAlert'
    import axios from 'axios'
    import qs from 'qs';
    import promise from 'es6-promise'
    import verifycode from '../../../components/VerifyCode'
    promise.polyfill();
    export default {
        metaInfo: {
            title: '福分通-用户登录', // set a title
            meta: [{                 // set meta
                name: '福分通',
                content: '专注企业员工福利'
            }]
        },
        data() {
            return {
                card_num:'',
                card_pwd:'',
                phone: '',
                password: '',
                site:'',
                access: false,
                isAccess:false
            }
        },
        components: {
            TopTitle,
            FAlert,
            verifycode
        },
        computed:{
          formCheck(){
              let data = {
                  card:this.card_num,
                  pwd:this.card_pwd,
                  phone:this.phone,
                  verify:this.password
              };
              for (let i in data){
                  if (!data[i]){
                      return false
                  }
              };
              return true
          }
        },
        watch:{
            phone:{
                handler(e){
                    let testPhone = /^1([358][0-9]|4[579]|66|7[0135678]|9[89])[0-9]{8}$/;
                    this.access = testPhone.test(e);
                },
                deep:true
            }
        },
        methods: {
            /**
             * 1.获取充值卡
             * 2.注册并且登录获取用户信息
             * 3.给用户充值
             * 4.获取子站信息并且跳转到对应子站
             * */
            checkInput(){
                if (this.formCheck){
                    this.cardInfo()
                } else{
                    this.bus.$emit('notice','请检查是否填写完整')
                }
            },
            cardInfo(){
                let data = {card_num:this.card_num};
                this.yum('api/member/getCardInfo',data).then(res=>{
                    if (res.data.status === 200){
                        this.site = res.data.data.business_code;
                        this.login()
                    }else{
                        this.bus.$emit('notice',res.data.msg);
                    }
                })
            },
            login(){
                let data ={
                    mobile:this.phone,
                    verify_code:this.password
                };
                for (let i in data){
                    if (!data[i]){
                        this.bus.$emit('notice','请检查账号,验证码是否填写！');
                        return false
                    }
                }
                data = qs.stringify(data);
                let url = `http://mapi.fufentong.com/${this.site}/api/member/register`;
                axios.post(url,data).then(res=>{
                    if (res.data.status === 200){
                        this.store.addStorage('FFT',res.data.data);
                        this.cardCharge()
                    }else{
                        this.bus.$emit('notice',res.data.msg);
                    }
                })
            },
            cardCharge(){
                let data ={
                    account:this.card_num,
                    password:this.card_pwd,
                    search:0,
                    token:this.store.getStorage('FFT').token
                }
                data = qs.stringify(data);
                let url = `http://mapi.fufentong.com/${this.site}/api/member/cardCharge`
                axios.post(url,data).then(res=>{
                    if (res.data.status === 200){
                        this.bus.$emit('notice','充值成功');
                        this.getCurrentBusiness();
                    }else{
                        this.bus.$emit('notice',res.data.msg)
                    }
                }).catch(err=>{
                    this.bus.$emit('notice','网络错误');
                })
            },
            getCurrentBusiness(){
                axios.post(`http://mapi.fufentong.com/${this.site}/api/business/getCurrentBusiness`)
                    .then(res=>{
                        if (res.data.status === 200){
                            this.store.addStorage('business_data',res.data.data);
                            setTimeout(()=>{
                                location.assign('../index.html')
                            },1500)
                        }
                    })
            }
        }
    }
</script>

<style lang="stylus" scoped>

    .login-bg
        width 100%
        min-width 1200px
        min-height 900px
        height 100%
        position absolute
        top 0
        left 0
        z-index -1
        background-position 50% 0
        background-repeat no-repeat
        background-size 1920px 974px
        background-color black
        text-align center
        .login-box
            display block
            margin 210px auto 200px
            width 480px
            background-color rgba(254,254,254,0.6)
            border-radius 6px
            box-shadow  0 0 8px 8px rgba(0,0,0,0.3)
            .box-title
                padding 20px
                font-size 24px
                font-weight bold
                color black
                border-bottom 1px solid #ccc
            .box-form
                display block
                padding 40px 50px
                .form-item
                    position relative
                    width 100%
                    height 50px
                    margin-bottom 20px
                    text-align left
                    transition 0.3s
                    img.icon
                        width 20px
                        top 15px
                        left 10px
                        display inline-block
                        position absolute
                    input
                        width 100%
                        height 100%
                        outline none
                        border solid 1px #999999
                        border-radius 4px
                        padding-left 10px
                        font-size 14px
                        opacity 0.8
                    input[name= '验证码']
                        width 240px
                        position absolute
                        bottom 0
                        opacity 0.8
                    input[type= button]
                        padding-left 0
                        background-color rgba(255,0,119,0.8)
                        border none
                        color white
                        font-size 18px
                        font-weight bold
                        cursor pointer
                    .business-list
                        position absolute
                        box-sizing border-box
                        width 100%
                        border 1px solid #999
                        border-radius 4px
                        background-color white
                        z-index 5
                        max-height 200px
                        overflow-y scroll
                        li
                            padding 10px
                            transition 0.3s
                            &:hover
                                cursor pointer
                                color white
                                background-color #ff0077
            .message
                .decode
                    position absolute
                    bottom 0
                    right 0
                    cursor pointer
                    font-size 16px
                    user-select none
                    text-align center
                    display inline-block
                    height 50px
                    width 130px
                    line-height 48px
                    margin-left 10px
                    border solid 1px #999999
                    border-radius 4px
                    opacity: 0.6;
                    background-color: #ff0077;
                    color: white;
            .save-login
                text-align left
                user-select none
        .login-info
            border 1px solid rgba(0, 0, 0, 0)
            margin-top 50px
            position absolute
            width 100%
            bottom 50px
            p
                color white
                a
                    color white
</style>
